* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background: radial-gradient(circle at 10% 20%, #000 0%, #4d4d4d 90.2%);
  padding: 30px;
  line-height: 1.5;
}

img {
  width: 379px;
  height: 30vh;
  object-fit: cover;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

.card {
  background-color: #ddd;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.info {
  justify-content: space-around;
  padding: 10px;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border-radius: 5px 0 0 5px;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-form button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  display: flex;
}

.load-more {
  -webkit-appearance: button;
  -ms-appearance: button;
  appearance: button;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  text-align: center;
  text-transform: none;
  touch-action: manipulation;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  background-color: #000;
  background-image: none;
  border: 1px solid #000;
  border-radius: 4px;
  margin: 0 auto;
  padding: 12px 40px;
  font-family: ITCAvantGardeStd-Bk, Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  display: block;
  overflow: visible;
  box-shadow: 4px 4px #fff, 4px 4px 0 1px #000;
}

.load-more:focus, .load-more:hover {
  text-decoration: none;
}

.load-more:active {
  outline: 0;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, .125);
}

.load-more:not([disabled]):active {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px #fff, 2px 2px 0 1px #000;
}

@media (min-width: 768px) {
  .load-more {
    padding: 12px 50px;
  }
}

